<template>
  <div>
    <div
      v-show="showMenu"
      class="menu"
      :style="{
        left: `${x}px`,
        top: `${y}px`
      }"
      @mousedown.prevent=""
    >
      <span
        class="item"
        @mousedown.prevent="doHighliht()"
      >
        Highlight
      </span>
      <span
        class="item"
        @mousedown.prevent="doTTS()"
      >
        TTS
      </span>
      <!-- You can add more buttons here -->
    </div>

    <div id="hlsss"></div>

    <!-- The insterted text should be displayed here -->
    <slot/>
  </div>
</template>

<script>
export default {
    data () {
        return {
            x: 0,
            y: 0,
            showMenu: false,
            selectedText: '',
            childIframe: {},
        }
    },

    mounted () {

        window.addEventListener("message",this.receiveMsgFromIframe);
        this.childIframe = document.getElementById("myframe");
        //iframe load 이벤트 리스너 등록
        //const iframeContent = document.getElementById('myframe');
        //iframeContent.addEventListener('load', this.handleIframeLoad);
    },

    beforeUnmount () {
        //console.log("umount");
        //window.removeEventListener('mouseup', this.onMouseup)
    },

    methods: {
        // iframe 로드가 완료되었을 때 실행되는 메소드
        handleIframeLoad(){
          this.iframeLoaded = true;

          // iframe 내의 mouseup 이벤트 리스너 설정
          if (this.iframeLoaded) {
            const iframeContent = document.getElementById('myframe').contentWindow.document;
            iframeContent.addEventListener('mouseup', this.onMouseup);
          }
        },

        receiveMsgFromIframe(e){
            let receiveData;

            try {
              receiveData = JSON.parse(e.data);
            } catch (e) {
              return;
            }

            if(receiveData.instruction == "hide tool") {
              this.showMenu = false;
              return;
            }
            else if(receiveData.instruction == "show tool") {

              // if(this.showMenu) {
              //   this.showMenu = false;
              //   return;
              // }

              const iframeRect = document.getElementById('myframe').getBoundingClientRect();
              const iframeTop = window.pageYOffset + iframeRect.top;
              const { x, y, width } = receiveData.dragPosition;

              //툴바 표시 위치 값 계산 후 툴바 표시
              this.x = x + (width / 2);
              this.y = y + iframeTop - 10;
              this.selectedText = receiveData.dragText;
              this.showMenu = true;
              return;
            }
            else {
              this.showMenu = false;
              return;
            }
        },

        //mouseup 이벤트 리스너 함수
        onMouseup () {
            //iframe dom 얻기
            const IframeDom = document.getElementById("myframe").contentDocument;

            //툴바 메뉴가 열려있는 상태라면 메뉴 닫고 종료
            if(this.showMenu){
              this.showMenu = false;
              IframeDom.getSelection().removeAllRanges();
              return;
            }

            //iframe내에서 selection 객체 얻기
            const selection = IframeDom.getSelection()

            //드래그 한 경우가 아닐경우(클릭 등), return 처리
            if(selection.isCollapsed){
              this.showMenu = false;
              IframeDom.getSelection().removeAllRanges();
              return;
            }

            //selection객체를 이용하여 Range객체 얻기
            const selectionRange = selection.getRangeAt(0)

            //드래그 시작 노드, 종료 노드 얻어 시작노드와 종료노드 다를 경우 기능 막을 때 필요(추후고려) 
            // const startNode = selectionRange.startContainer.parentNode
            // const endNode = selectionRange.endContainer.parentNode

            // if (!startNode.isSameNode(endNode)) {
            //     this.showMenu = false
            //     return
            // }

            //드래그 Range로 부터 Rect 추출 후 x y width 가져오기
            const { x, y, width } = selectionRange.getBoundingClientRect();
            
            //iframe top 위치 구하기
            const iframeRect = document.getElementById('myframe').getBoundingClientRect();
            const iframeTop = window.pageYOffset + iframeRect.top;

            //width가 0이면 종료
            if (!width) {
                this.showMenu = false
                return
            }

            //툴바 표시 위치 값 계산 후 툴바 표시
            this.x = x + (width / 2)
            this.y = y + iframeTop - 10
            this.selectedText = selection.toString()
            this.showMenu = true
        },

        // handleAction (action) {
        //     this.$emit(action, this.selectedText)
        // },


        //하이라이팅 메소드
        doHighliht(){
          this.showMenu = false;
          this.childIframe.contentWindow.postMessage('highlight', '*');
          // //iframeDom, IframeWindow 얻기
          // const IframeDom = document.getElementById("myframe").contentDocument;
          // const IframeWindow =  document.getElementById("myframe").contentWindow;

          // //드래그 상태 아니면 종료
          // if(IframeDom.getSelection().isCollapsed){
          //     return;
          // }
          
          // //드래그 영역 Rects 얻기 
          // const rectList = IframeDom.getSelection().getRangeAt(0).getClientRects();

          // for(const rect of rectList){

          //   //rect top left 좌표 및 height, width 값 추출
          //   const top = rect.top + IframeWindow.pageYOffset + "px";
          //   const left = rect.left + IframeWindow.pageXOffset + "px";
          //   const height = rect.height + "px";
          //   const width = rect.width + "px";

          //   //하이라이팅 element
          //   const hlBox = `<a xmlns="http://www.w3.org/1999/xhtml" href="#" class="highlight" style="position: absolute;top:${top}; left: ${left}; height: ${height};width: ${width};z-index: 1000;background-color: blue;opacity: 0.3;"></a>`;

          //   //iframe에 body에 하이라이팅 element 삽입
          //   IframeDom.body.innerHTML += hlBox;
          //   console.log(rect);
          //}
          

          //선택영역 제거
          // IframeDom.getSelection().removeAllRanges();
        },


        //TTS기능 함수
        doTTS(){

          if(this.selectedText === ''){
            return;
          }

          const synth = window.speechSynthesis;
          const utterance = new SpeechSynthesisUtterance(this.selectedText);

          // TTS 설정을 원하는대로 변경할 수 있습니다.
          utterance.lang = 'ko';
          utterance.volume = 1;
          utterance.rate = 1;
          utterance.pitch = 1;

          // TTS 실행
          synth.speak(utterance);

          this.childIframe.contentWindow.postMessage('remove select', '*');
          this.showMenu = false;
        }
    }
}
</script>

<style scoped>
.menu {
  height: 30px;
  padding: 5px 10px;
  background: #333;
  z-index: 99999;
  border-radius: 3px;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, -100%);
  transition: 0.2s all;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu:after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: -5px;
  transform: translateX(-50%);
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid #333;
}

.item {
  color: #FFF;
  cursor: pointer;
}

.item:hover {
  color: #1199ff;
}

.item + .item {
  margin-left: 10px;
}
</style>